.about{
    height: 30vh;
}

.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%;
}

.about__me{
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about__content p{
    margin: 2rem 0 2rem;
    color:var(--color-light)
}


@media screen and (max-width:1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap:0;
    }

    .about__me{
        width: 50%;
        margin:2rem auto 4rem;
    }

    .about__content p{
        margin:1rem 0 1.5rem;
    }
}

@media screen and (max-width:600px) {
    .about__me{
        width:65%;
        margin:0 auto 3rem;
    }

    .about-content{
        text-align: center;
    }
    .about-content p{
        margin:1.5rem 0;
    }
}