header{
    height: 100vh;
    padding-top:7rem;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate-reverse;
}
@keyframes move{
    50%{transform: translateY(-50px);}
    100%{transform: translateY(-100px);}
   
    
}

.i-title{
    height:50px;
    overflow: hidden;
}

.i-title-item{
    height:50px;
    font-size:30px;
    font-weight:bold;
    color: var(--color-primary);
    align-items: center;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cta{
    margin-top:2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left:0;
    bottom:3rem;
}

.header__socials::after{
    content:'';
    width:1px;
    height:2rem;
    background: var(--color-primary);
}


.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding:0em 4.5em;
}
img{
    
}





.scroll__down{
    position: absolute;
    right:-2.5rem;
    bottom:5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px) {
    header{
        height:68vh;
    }
}

@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
    .header__socials,.scroll__down{
        display: none;
    }
}